<template>
  <el-container>
<!--    -->
    <el-header style="text-align: left">    <!---->
      <div class="el-icon-folder" :style="{Height: Height + 'px'}">PM-</div>
    </el-header>
    <!--  -->
    <uploader
      ref="uploader"
      :options="options"
      :autoStart=false
      :file-status-text="fileStatusText"
      @file-added="onFileAdded"
      @file-success="onFileSuccess"
      @file-error="onFileError"
      class="uploader-ui">
      <uploader-drop>
        <div>
          <uploader-btn id="global-uploader-btn" :attrs="attrs" ref="uploadBtn">
            <i class="el-icon-upload el-icon--right"></i>
          </uploader-btn>
        </div>
      </uploader-drop>
      <uploader-list></uploader-list>
    </uploader>
  </el-container>
</template>

<script>
import {ACCEPT_CONFIG} from '../../components/common/config';
import SparkMD5 from 'spark-md5';
import {mergeFile} from "@/api/modules/uploadFile";
import store from '../../store'

export default {
  data () {
    return {
      options: {
        // URLPOST
        target: `http://${process.env.BACKEND_API_HOST}`+"/uploader/chunk",
        //()
        chunkSize: '2048000',
        //chunkMultipartfile
        fileParameterName: 'upfile',
        //
        maxChunkRetries: 3,
        //GETtarget URL
        testChunks: true,
        /*
        ,Uploader.Chunk
        reponsesuccessStatuses
        reponsepermanentErrors onFileError 
        reponse
        checkChunkUploadedByResponsereturn true
        */
        //
        headers:{'Authentication':store.state.account.token},

        checkChunkUploadedByResponse: function (chunk, response_msg) {
          let objMessage = JSON.parse(response_msg);
          if (objMessage.skipUpload) {
            return true;
          }
          return (objMessage.uploadedChunks || []).indexOf(chunk.offset + 1) >= 0;
        }
      },
      attrs: {
        // 
        accept: ACCEPT_CONFIG.getAll()
      },
      fileStatusText: {
        success: '',
        error: '',
        uploading: '',
        paused: '',
        waiting: ''
      },
    }
  },
  methods: {
    onFileAdded(file) {
      this.computeMD5(file);
    },
    /*
     rootFile  Uploader.File 
     file  Uploader.File 
     message 
     chunk  Uploader.Chunk chunk.xhr.status
    */
    // onFileSuccess(rootFile, file, response, chunk) {
    //   //refProjectId
    //   file.refProjectId = "123456789";
    //   mergeFile(file).then( responseData=> {
    //     if(responseData.data.code === 415){
    //       console.log(""+responseData.data.code);
    //     }
    //   }).catch(function (error){
    //     console.log(""+error);
    //   });
    // },
    // onFileError(rootFile, file, response, chunk) {
    //   console.log(''+response);
    // },

    onFileSuccess(rootFile, file, response, chunk) {
      //refProjectId
      file.refProjectId = "123456789";
      mergeFile(file).then( responseData=> {
        if(responseData.data.code === 415){
          console.log(""+responseData.data.code);
        }
      }).catch(function (error){
        console.log(""+error);
      });
    },
    onFileError(rootFile, file, response, chunk) {
      console.log(''+response);
    },

    /**
     * md5,
     * @param file
     */
    computeMD5(file) {
      file.pause();

      //5G
      let fileSizeLimit = 5 * 1024 * 1024 * 1024;
      console.log(""+file.size);
      console.log(""+fileSizeLimit);
      if(file.size > fileSizeLimit){
        this.$message({
          showClose: true,
          message: '5G'
        });
        file.cancel();
      }

      let fileReader = new FileReader();
      let time = new Date().getTime();
      let blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;
      let currentChunk = 0;
      const chunkSize = 10 * 1024 * 1000;
      let chunks = Math.ceil(file.size / chunkSize);
      let spark = new SparkMD5.ArrayBuffer();
      //Md51md5
      let chunkNumberMD5 = 1;

      loadNext();

      fileReader.onload = (e => {
        spark.append(e.target.result);

        if (currentChunk < chunkNumberMD5) {
          loadNext();
        } else {
          let md5 = spark.end();
          file.uniqueIdentifier = md5;
          file.resume();
          console.log(`MD5${file.name} \nMD5${md5} \n${chunks} :${file.size} ${new Date().getTime() - time} ms`);
        }
      });

      fileReader.onerror = function () {
        this.error(`${file.name}`)
        file.cancel();
      };

      function loadNext() {
        let start = currentChunk * chunkSize;
        let end = ((start + chunkSize) >= file.size) ? file.size : start + chunkSize;

        fileReader.readAsArrayBuffer(blobSlice.call(file.file, start, end));
        currentChunk++;
        console.log(""+currentChunk+"");
      }
    },
    close() {
      this.uploader.cancel();
    },
    error(msg) {
      this.$notify({
        title: '',
        message: msg,
        type: 'error',
        duration: 2000
      })
    }
  }
}
</script>

<style>
.uploader-ui {
  padding: 25px;
  margin: 40px auto 0;
  font-size: 20px;
  font-family: Microsoft YaHei;
  box-shadow: 0 0 10px rgba(0, 0, 0, .4);
  height: 200px;
  width: 600px;
}
.uploader-ui .uploader-btn {
  margin-right: 4px;
  font-size: 12px;
  border-radius: 3px;
  color: #FFF;
  background-color: #409EFF;
  border-color: #409EFF;
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
}
.uploader-ui .uploader-list {
  max-height: 440px;
  overflow: auto;
  overflow-x: hidden;
  overflow-y: auto;
}

.el-header, .el-footer {
   background-color: #B3C0D1;
   color: #333;
   text-align: center;
   line-height: 60px;
 }
  .el-container {
    height: 100%;
  }
</style>



