import axios from 'axios';
import store from '../store'

const service = axios.create({
  // axiosbaseURLURL
  baseURL: `http://${process.env.BACKEND_API_HOST}/`,
  //
  timeout: 20000
});

//vue
//token
service.interceptors.request.use((config) => {
    if (store.state.account.token) {
      config.headers.Authentication = store.state.account.token
    }
    return config;
  },
  error => {
    console.log(error);
    return Promise.reject();
  }
);

// 200
service.interceptors.response.use(
  response => {
    if (response.status === 200) {
      return response.data; //
    } else {
      Promise.reject();
    }
  },
  error => {
    console.log(error);
    return Promise.reject();
  }
);

export const requestApi = (option) => {
  if (option.isJson && JSON.stringify(option.data) !== {}) {
    return service({
      method: option.method,
      url: `${option.url}`,
      data: option.data,
      headers: {//json
        'Content-Type': 'application/json',
        'Authentication':store.state.account.token
      }
    })
  } else if (option.isJson && JSON.stringify(option.data) === {}) {
    return service({
      method: option.method,
      url: `${option.url}`,
      data: option.data,
      headers: {
        'Content-Type': 'application/json',
        'Authentication':store.state.account.token
      }
    })
  } else if (!option.isJson && JSON.stringify(option.data) !== {}) {
    return service({
      method: option.method,
      url: `${option.url}`,
      params: option.data,
      headers: {//key1=value1,key2=value2
        'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8',
        'Authentication':store.state.account.token,
      },
      responseType: 'blob'
    })
  } else if (!option.isJson && JSON.stringify(option.data) === {}) {
    console.log(option);
    return service({
      method: option.method,
      url: `${option.url}`
    })
  }
}




